'use client';
import * as THREE from 'three';

export type CelestialObject = {
  id: string;
  name: string;
  type: 'star' | 'planet' | 'region' | 'comet';
  size: number;
  distance: number; // Semi-major axis (a) in AU for planets, used for scaling here
  color: string;
  orbitalSpeed: number; // Now represents period in Earth years
  rotationSpeed: number;
  rings?: { innerRadius: number; outerRadius: number; textureUrl: string; };
  eccentricity?: number;
  description?: string;
  diameter?: number; // in km
  mass?: string; // in 10^24 kg
  dayLength?: number; // in hours
  orbitCurve?: THREE.EllipseCurve;
  orbitalInclination?: number;
  orbitalOffset?: number; // Initial position offset, will be replaced by time-based calculation
  discoveryYear?: number;
  perihelion?: number; // in AU
  aphelion?: number; // in AU


  // Keplerian Elements for accurate orbit calculation
  semiMajorAxis: number; // a, in AU
  meanLongitude: number; // L, in degrees at J2000
  longitudeOfPerihelion: number; // ϖ, in degrees
  longitudeOfAscendingNode: number; // Ω, in degrees
};

